import React from "react";
import styled from "styled-components";

const Table = () => (
  <Wrap>
    <TableHead>
      <p>Название</p>
      <p>Тип</p>
      <p>Описание</p>
    </TableHead>
    {mokeTableRow.map(el => {
      const { name, type, desc } = el;
      return (
        <TableRow key={name}>
          <h5>{name}</h5>
          <p>{type}</p>
          <p>{desc}</p>
        </TableRow>
      );
    })}
  </Wrap>
);

export default Table;

const mokeTableRow = [
  {
    name: "jivo_onLoadCallback",
    type: "void",
    desc: "Вызывается при завершении инициализации виджета JivoSite"
  },
  {
    name: "jivo_onOpen",
    type: "void",
    desc: "Вызывается при открытии окна диалога JivoSite"
  },
  {
    name: "jivo_onClose",
    type: "void",
    desc: "Сворачивание окна диалога JivoSite"
  },
  {
    name: "	jivo_onIntroduction",
    type: "void",
    desc: "Посетитель ввёл контактные данные"
  },
  {
    name: "jivo_onResizeCallback",
    type: "void",
    desc: "Коллбек, срабатывающий при любом изменении размеров виджета"
  }
];

const Wrap = styled.div`
  border: 1px solid #e0e6ed;
  margin: 0 0 5.3rem 0;
`;

const TableHead = styled.div`
  background-color: #f9fafc;
  border-bottom: 1px solid #e0e6ed;
  min-height: 5.3rem;
  display: grid;
  grid-template-columns: minmax(min-content, 16.2rem) minmax(min-content, 4rem) 1fr;
  grid-column-gap: 3.9rem;
  padding: 0 2.7rem 0 2.1rem;
  align-items: center;
`;

const TableRow = styled.div`
  display: grid;
  grid-template-columns: minmax(min-content, 16.2rem) minmax(min-content, 4rem) 1fr;
  grid-column-gap: 3.9rem;
  padding: 0 2.7rem 0 2.1rem;
  margin: 2rem 0;
  align-items: baseline;
`;



