import { injectGlobal } from "styled-components";

injectGlobal`
	html {
		font-size: 10px;
	}

  body {
		margin: 0;
	}
	
	:root {
		--text-color-primary: #23233b;
		--text-color-secondary: #6b7b93;
		--bg-color-active: #00bf59;
		--bg-color-hover: #eff2f8;
	}

	h1, h2, h3, h4, h5, h6, p, menu, ul, li, a, span {
		font-family: Circe;
		margin: 0;
		padding: 0;
		text-decoration: none;
		list-style-type: none;
		line-height: 1;
	}
	
	h1, h2, h3, h4, h5, h6, span {
		color: var(--text-color-primary);
	}

	a, p {
		color: var(--text-color-secondary)
	}

	h1 {
		font-size: 4.6rem;
		font-weight: 700;
		line-height: 6.5rem;
	}

	h2 {
		font-size: 3rem;
		font-weight: 700;
	}
	
	h3 {
		font-size: 2rem;
		font-weight: 700;
	}

	h4 {
		font-size: 1.7rem;
		font-weight: 700;
	}

	h5 {
		font-size: 1.7rem;
		font-weight: 300;
		color: #676cff;
	}

	h6 {
		font-size: 1.4rem;
		font-weight: 400;
		color: #aec1dc;
	}

	span {
		font-size: 1.7rem;
		font-weight: 700;
		line-height: 29.92px;
	}

	p {
		font-size: 1.7rem;
		font-weight: 300;
		hyphens: auto;
		line-height: 2.9rem;
	}

	a {
		font-size: 1.5rem;
		font-weight: 400;
		cursor: pointer;
	}

	@media (max-width: 1024px) {
		html {
			font-size: 9px
		}
	}

	@media (max-width: 768px) {
		html {
			font-size: 7px
		}
	}
`;

export default {};



