import React, { Component } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Link } from "react-router-dom";

class MenuMethods extends Component {
  state = {
    isOpen: false,
    replaceMethods: [],
    activeLink: ""
  };

  componentDidMount() {
    const {
      methods,
      location: { pathname }
    } = this.props;
    const replaceMethods = methods.map(element => element.replace(/\s/g, ""));
    const activeLink = this.replaceSlash(pathname);
    const openMenu = replaceMethods.some(el => el === activeLink);

    this.setState({
      isOpen: openMenu,
      replaceMethods: replaceMethods,
      activeLink: activeLink
    });
  }

  componentDidUpdate(prevProps) {
    const {
      location: { pathname }
    } = this.props;
    const prevPath = prevProps.location.pathname;
    if (pathname !== prevPath) {
      const activeLink = this.replaceSlash(pathname);
      this.setState({
        activeLink: activeLink
      });
    }

    if (pathname !== prevPath && pathname === "/") {
      this.setState({
        isOpen: false
      });
    }
  }

  replaceSlash = text => text.replace(/\//, "");

  handlerClick = () => {
    const { isOpen } = this.state;
    this.setState({ isOpen: !isOpen });
  };

  render() {
    const { title, methods } = this.props;
    const { isOpen, replaceMethods, activeLink } = this.state;
    return (
      <Wrap>
        <H4 onClick={this.handlerClick} isOpen={isOpen}>
          {title}
        </H4>
        <Menu>
          {isOpen &&
            methods.map((element, index) => {
              return (
                <li key={replaceMethods[index]}>
                  <A to={replaceMethods[index]} pathname={activeLink}>
                    {element}
                  </A>
                </li>
              );
            })}
        </Menu>
      </Wrap>
    );
  }
}
export default MenuMethods;

MenuMethods.propTypes = {
  title: PropTypes.string.isRequired,
  methods: PropTypes.array.isRequired,
  location: PropTypes.object
};

const Wrap = styled.div`
  margin: 0 0 5.5rem 0;
`;

const H4 = styled.h4`
  cursor: pointer;
  position: relative;

  :before {
    content: "";
    position: absolute;
    height: 1.2rem;
    width: 1.2rem;
    top: ${({ isOpen }) => (isOpen ? "0.2rem" : "-0.2rem")};
    right: 0.2rem;
    border-bottom: 2px solid black;
    border-right: 2px solid black;
    transform: ${({ isOpen }) => (isOpen ? "rotate(225deg)" : "rotate(45deg)")};
  }
`;

const Menu = styled.menu`
  margin: 1.3rem 0 0 0;
`;

const A = styled(Link)`
  padding: 1rem 0 1.1rem 2.1rem;
  margin: 0px 0 0.5rem 0;
  display: block;
  border-radius: 5px;

  :hover {
    background: var(--bg-color-hover);
  }

  ${({ to, pathname }) => {
    if (to === pathname) {
      return `
				background: var(--bg-color-active);
				color: #ffffff;
				
				:hover {
					background: var(--bg-color-active);
					color: #ffffff;
				}
				`;
    }
  }};
`;



