import React from "react";
import styled from "styled-components";

const Function = () => (
  <Container>
    {mokeData.map(el => {
      const { title, desc, name } = el;
      return (
        <FunctionContainer key={title}>
          <FunctionTitle>{title}</FunctionTitle>
          <PFunctionDesc>{desc}</PFunctionDesc>
          <FunctionName>{name}</FunctionName>
        </FunctionContainer>
      );
    })}
  </Container>
);

export default Function;

const mokeData = [
  {
    title: "chatMode",
    desc:
      "С помощью этого метогода можно получить текущее состояние операторов.",
    name: "jivo_api.chatMode();"
  },
  {
    title: "getContactInfo",
    desc:
      "Считывает данные посетителя в виде структуры contact_info, которые посетитель ввел в форме представления. Если посетитель не заполнил какие-либо поля, в их значении будет null.",
    name: "jivo_api.chatMode();"
  }
];

const Container = styled.div`
  grid-area: func / func / func / threeFunct;
`;

const FunctionContainer = styled.div`
  grid-column: 1 / -1;
  display: grid;
  grid: "title ." "desc name" / minmax(55rem, 1fr) minmax(min-content, 33.1rem);
  grid-row-gap: 2rem;
  margin: 0 0 4.6rem 0;

  @media (max-width: 1024px) {
    grid:
      "title"
      "desc"
      "name"
      /1fr;
  }
`;

const FunctionTitle = styled.h3`
  grid-area: title;
  margin: 0 6rem;
`;

const PFunctionDesc = styled.p`
  grid-area: desc;
  margin: 0 6rem;
`;

const FunctionName = styled.p`
  font-size: 1.5rem;
  color: #ffffff;
  grid-area: name;
  z-index: 1;
  margin: 0 0 0 3.3rem;
	
  @media (max-width: 1024px) {
		margin: 0 6rem 0 3.3rem;
		padding: 0 0 0 2.7rem
    background-color: #23233b;
  }
`;



