import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

import MenuMethods from "./columnOne/MenuMethods";

import logo from "../assets/img/logo.png";

const ColuemnOne = props => (
  <ColumnOne>
    <ContainerLogo>
      <Link to="/">
        <LogoImg src={logo} alt="logo" />
      </Link>
      <H6>for developers</H6>
    </ContainerLogo>
    <Sticky>
      <MenuMethods title={"Javascript API"} methods={mokeMenuApi} {...props} />
      <MenuMethods title={"Webhooks"} methods={mokeMenuWebhooks} {...props} />
    </Sticky>
  </ColumnOne>
);

export default ColuemnOne;

const mokeMenuApi = [
  "Methods and functions",
  "chatMode",
  "getContactInfo",
  "getVisitorNumber Selected",
  "setContactInfo",
  "setCustomData",
  "setRules Hover",
  "setUserToken",
  "showProactiveInvitation"
];

const mokeMenuWebhooks = [
  "Events",
  "chat_accepted",
  "chat_finished",
  "offline_message"
];

const ColumnOne = styled.div`
  grid-area: one;
  background-color: #f9fafc;
  padding: 3rem 3rem 0;
`;

const LogoImg = styled.img`
  margin: 0 0 0.4rem 0.5rem;
`;

const ContainerLogo = styled.div`
  margin: 0 0 3.7rem 0;
`;

const H6 = styled.h6`
  margin: 0 0 0 0.4rem;
`;

const Sticky = styled.div`
  position: sticky;
  top: 2rem;
`;



