import React from "react";
import styled from "styled-components";

import Table from "./columnTwo/Table";

const ColuemnTwo = () => (
  <ColumnTwo>
    <H1>JivoSite API для разработчиков</H1>
    <PAfterTitle>
      JivoSite предоставляет разработчикам два набора инструментов для
      интеграции:
    </PAfterTitle>
    <PSpan>
      <Span>Клиентская часть - JavaScript API виджета,</Span>
      позволяющее управлять виджетом, который отображается пользователю. С
      помощью него можно управлять окном чата, а так же передавать произвольную
      информацию со страницы, на которой размещено окно чата, в приложение
      оператора (например, можно передать дополнительную информацию о
      пользователе, которая так же будет сохранена в журнале диалогов).
    </PSpan>
    <PSpan>
      <Span>Серверная часть - Webhooks API,</Span>
      позволяющее получать уведомления о начале или завершении чата на стороне
      вашего сервера, а так же передавать в JivoSite дополнительную информацию
      для отображения в приложени оператора в ответе на HTTP-запросы.
    </PSpan>
    <H2>JavaScript API</H2>
    <H3>Methods and functions</H3>
    <PMethodsDesc>
      JivoSite вызывает перечисленные ниже функции на странице, чтобы сообщить о
      возникновении события. Вы можете объявить на странице любую из этих
      функций и выполнять в ней логику обработки возникшего события. Например,
      по событию jivo_onIntroduction вы можете получить введенные клиентом
      контактные данные.
    </PMethodsDesc>
    <p>Сallback-функции</p>
    <Table />
  </ColumnTwo>
);

export default ColuemnTwo;

const ColumnTwo = styled.div`
  grid-area: two;
  padding: 2.3rem 5.8rem 0 6rem;
`;

const H1 = styled.h1`
  margin: 0 0 2.5rem 0;
`;

const PAfterTitle = styled.p`
  margin: 0 0 1.7rem 0.3rem;
`;

const PSpan = styled.p`
  margin: 0 0 2rem 0.3rem;
`;

const H2 = styled.h2`
  margin: 8.4rem 0 3.7rem 0;
`;

const H3 = styled.h3`
  margin: 0 0 2.2rem 0;
`;

const PMethodsDesc = styled.p`
  margin: 0 0 4.1rem 0;
`;

const Span = styled.span`
  margin: 0 0.5rem 0 0;
`;



